<template>
  <div class="login" >
    <div class='dr' v-if='fff==true'>
      <h3>登入账户</h3>
      <el-row class="lang-box">
        <el-col :span="24">
          <el-input 
            placeholder="请输入用户名" 
            suffix-icon="el-icon-user-solid"
            v-model='name'
          ></el-input>
        </el-col>
        <el-col :span="24">
          <el-input 
            placeholder="请输入密码" 
            suffix-icon="el-icon-unlock"
            v-model='password'
            :show-password='true'
          ></el-input>
        </el-col>
        <el-button type="success" @click='loin'>登入</el-button>
        <span @click="qzc">去注册</span>
      </el-row>
    </div>
    <div class='zc' v-else>
      <h3>注册账户</h3>
      <el-row class="lang-box">
        <el-col :span="24">
          <el-input 
          placeholder="请输入用户名" 
          suffix-icon="el-icon-user-solid"
          v-model='name'></el-input>
        </el-col>
        <el-col :span="24">
          <el-input 
          placeholder="请输入密码" 
          suffix-icon="el-icon-unlock"
          v-model='password'
          :show-password='true'
          ></el-input>
        </el-col>
        <el-col :span="24">
          <el-input 
          placeholder="请再输入密码" 
          suffix-icon="el-icon-unlock"
          v-model='password2'
          :show-password='true'></el-input>
        </el-col>
        <el-button 
        type="success"
        @click='regist'
        >注册</el-button>
        <span @click="qzc">返回登入</span>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      fff:true,
      name:'',
      password:'',
      password2:''
    }
  },
  methods:{
    qzc(){
      if(this.fff==true){
        this.fff=false
      }
      else{
        this.fff=true
      }
    },
    // 登入
    loin(){
      let data={
        username:this.name,
        password:this.password
      }
      this.$http.login(data).then(res=>{
        if(res.err==1){
          alert(res.msg )
          return
        }
        alert(res)
        localStorage.setItem("token", res.token);//保存token
        this.$router.replace('/home')
      })
    },
    // 注册
    regist(){
      let data={
        username:this.name,
        password:this.password,
        password2:this.password2,
      }
      this.$http.regist(data).then(res=>{
        if(res.err==2){
          alert(res.msg )
          return
        }
        alert(res.msg )
        this.fff=true
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.login {
  text-align: center;
  height: 100%;
  background: #00ccff;
  padding-top: 100px;
  .lang-box {
    margin: 10px auto;
    padding: 10px;
    text-align: center;
    width: 400px;

    .el-input {
      width: 380px;
      margin: 20px auto;
    }
    .el-button {
      width: 380px;
    }
    &>span{
      display: block;
      text-align:right;
      padding-top:10px;
      cursor: pointer;
    }
  }
}
</style>